import { useEffect, useState } from "react";
import PortfolioList from "../portfolioList/PortfolioList";
import "./myproject.scss"
import {projectportfolio,skillsportfolio} from "../../data";


export default function Myproject() {
    const [selected,setSelected] = useState("pro")
    const [data,setData] = useState([])
    const list = [
        {
            id: "pro",
            title: "Projects",
        },
        {
            id: "skil",
            title: "Skills",
        },
        
        
    ];

    useEffect(()=>{

        switch(selected){
            case "pro":
                setData(projectportfolio);
                break;
            case "skil":
                setData(skillsportfolio);
                break;
           
            default:
                setData(projectportfolio);
        }

    },[selected])
    return(
        <div className="myproject" id="project">
           <h1>Portfolio</h1>
           <ul>
            {list.map((item)=>(

               <PortfolioList 
               title={item.title} 
               active={selected === item.id} 
               setSelected={setSelected}
               id={item.id}/>
            ))}
           </ul>
           <div className="container">
               {data.map((d)=>(
               <div className="item">
                
                   <img 
                    src={d.img} 
                    alt=""
                    />
                 
                   <div className="desc"><h2>{d.title}</h2><br/>{d.text}</div>
                   
               </div>
               
                ))}
               
           </div>
          
        </div>
    )
}